@mixin on-tablet {
  @media (min-width: $tablet-min-width) {
    @content;
  }
}

@mixin on-desktop {
  @media (min-width: $desktop-min-width) {
    @content;
  }
}

@mixin content-padding-inline() {
  padding-inline: 16px;
  margin: 0 auto;
  
  @include on-tablet {
    padding-inline: 24px;
  }
  
  @include on-desktop {
    padding-inline: 40px;
    max-width: 1440px;
  }
}

@mixin hover($_property, $_toValue) {
  transition: #{$_property} 0.3s;
  &:hover {
    #{$_property}: $_toValue;
  }
}

@mixin grid {
  display: grid;
  column-gap: 8px;

  --columns: 2;

  grid-template-columns: repeat(var(--columns), 1fr);

  @include on-tablet {
    --columns: 6;
  }

  @include on-desktop {
    column-gap: 20px;
    --columns: 12;
  }
}

